import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";


export class FBFileOptions extends FBFieldWithPriceBaseOptions{
    public Value:string;
    public AllowMultipleFiles:boolean;
    public AllowedExtensions:string;
    public ButtonLabel:string;

    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.Value='';
        this.Type=FieldTypeEnum.FileUpload;
        this.AllowMultipleFiles=false;
        this.AllowedExtensions='';
        this.ButtonLabel='Add File';
        this.Label='File';

    }


}

