import './Style.css'
import * as React from "react";
import {
    FBFieldWithPriceBase,
    FBFieldWithPriceBaseProps,
    FBFieldWithPriceBaseState
} from "FormBuilder/Fields/Core/FBFieldWithPriceBase";
import {OptionsCalculator} from "FormBuilder/Core/Calulators/OptionsCalculator";
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FBFieldBase, FBFieldBaseProps, FBFieldBaseState} from "FormBuilder/Fields/Core/FBFieldBase";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";

import {GetFieldOptions} from "FormBuilder/Events/GetFieldOptions";
import {GetField} from "FormBuilder/Events/GetField";

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {css} from "emotion";
import {faPlusCircle} from "@fortawesome/free-solid-svg-icons/faPlusCircle";
import {faMinusCircle} from "@fortawesome/free-solid-svg-icons/faMinusCircle";
import {FBFileOptions} from "./FBFile.Options";
import {ToastService} from "shared/utilities/services/ToastService";
import {FBFileItem, FBFileModel} from "./FBFile.Model";
import {GetModel} from "FormBuilder/Events/GetModel";
import {FBHiddenModel} from "../FBHidden/FBHidden.Model";
import {FBHiddenOptions} from "../FBHidden/FBHidden.Options";
import {EventManager} from "shared/core/Events/EventManager";

export class FBFile extends FBFieldWithPriceBase<FBFileProps,{}>{

    constructor(props: FBFileProps) {
        super(props);
    }

    static defaultProps: Partial<FBFileProps> = {

    };


    public IsDragAndDropSupported()
    {
        var div = document.createElement('div');
        return (('draggable' in div) || ('ondragstart' in div && 'ondrop' in div)) && 'FormData' in window && 'FileReader' in window;
    }







    public SubRender() {
        if(this.props.Model.Options.ShowQuantitySelector)
        {
            return(
                this.props.Model.Options.QuantityPosition == "bottom" || this.props.Model.Options.QuantityPosition == "right" ?
                    <React.Fragment>
                        {this.GetInput(this.props.Model.Options.QuantityPosition)}
                        {this.GetQuantityInput()}

                        {this.GetPriceOrRequiredBox()}

                    </React.Fragment>:
                    <React.Fragment>
                        {this.GetQuantityInput()}
                        {this.GetInput(this.props.Model.Options.QuantityPosition)}

                        {this.GetPriceOrRequiredBox()}

                    </React.Fragment>
            )
        }
        return(
            <React.Fragment>
                {this.GetInput('')}

                {this.GetPriceOrRequiredBox()}

            </React.Fragment>
        )

    }

    GetQuantityInput(){
        return(
            <div className={'rnTextFieldQuantity '+ this.props.Model.Options.QuantityPosition}>
                <div className={'rednaoLabel'}>
                    <label style={{fontWeight:'bold'}}>{this.props.Model.Options.QuantityLabel}</label>
                </div>
                <input className={'rnInputQuantity'} placeholder={this.Model.Options.QuantityPlaceholder} style={{width:'100%'}} type={'text'} value={this.Model.Quantity}
                       onChange={x=>{this.Model.Quantity=this.Model.ParseNumber(x.target.value as any);this.Model.Refresh()}}/>
            </div>

        )
    }

    GetInput(additionalClassNames:string){
        return (
            <div style={{width:'100%'}} className={'rnList '+ additionalClassNames}>
                {this.GetLabel()}
                <table className={'rnFileContainer'} style={{width:'100%',borderCollapse:'collapse',border:"none",margin:0}}>
                    <tbody>
                        {this.GenerateRows()}
                    </tbody>
                </table>

            </div>

        );
    }






    private GenerateRows() {
        return this.Model.Files.map((row)=> {
            return(
                <tr key={row.Id} className={'rednao'}>
                    <td style={{border: "none",padding:'6px 0'}}>
                        <div style={{width:'100%'}}>

                            {row.File.length == 0 ?
                                <div style={{padding: 5, border: '1px dashed', cursor: 'pointer', flexGrow: 1}}
                                     className={'rnFilePlaceholder ' + (row.Highlight ? 'active' : '')} onClick={x => {
                                    row.Ref.click()
                                }} onDragLeave={x => this.OnDragExit(row)} onDragEnter={x => this.OnDragEnter(x, row)}
                                     onDragOver={x => this.OnDragOver(x, row)} onDrop={x => this.OnDrop(row,x)}>
                                    <span>{this.IsDragAndDropSupported()?'Drag or click here to add a file':'Click here to add a file'}</span>
                                </div> :
                                <div style={{padding:5,border: '1px solid #ccc',flexGrow:1}}>
                                    <span>{row.File[0].name}</span>
                                </div>
                            }
                        </div>
                        <input ref={row.FileRefUpdated} accept={this.Model.Options.AllowedExtensions} type={'file'} key={row.Id}
                               name={'rnProFile'+this.Model.Options.Id + '@' + row.Id} style={{display: "none"}} onChange={x=>this.FileChanged(row,x.target.files)}/>
                    </td>
                    <td style={{border:"none",width:25}}>
                        <div style={{marginLeft:5}}>
                            {row.File.length > 0 &&
                            <span onClick={() => this.RemoveFile(row)}>
                                                        <FontAwesomeIcon style={{fontSize: 18}}
                                                                         className={css`cursor:pointer;&:hover{color:red;}`}
                                                                         icon={faMinusCircle}/>
                                                    </span>
                            }
                        </div>
                    </td>
                </tr>
            )
        })
    }








    private OnDragOver(e: React.DragEvent<HTMLDivElement>,file:FBFileItem) {
        if(!this.IsDragAndDropSupported())
            return;
        e.preventDefault();
        if(file.Highlight==false)
        {
            file.Highlight=true;
            this.forceUpdate();
        }

    }

    private OnDrop(row: FBFileItem, x: React.DragEvent<HTMLDivElement>) {
        if(!this.IsDragAndDropSupported())
            return;
        x.preventDefault();
        row.Highlight=false;
        let file=x.dataTransfer.files;
        if(file.length==0)
        {
            this.RemoveFile(row);
            return;
        }

        if(file.length>0&&!this.ExtensionIsValid(file[0].name))
        {
            if(file.length>0) {
                row.File = [];
                row.Ref.value='';
            }
            return;
        }

        row.File=x.dataTransfer.files;
        row.Name=x.dataTransfer.files[0].name;
        row.Ref.files=row.File;
        if(this.Model.Files.filter(x=>x.File.length==0).length==0)
        {
            this.Model.Files.push(this.Model.CreateRow());
        }

        this.forceUpdate();

    }

    private ExtensionIsValid(name:string)
    {
        name=name.toLowerCase().trim();
        let extensions=this.Model.Options.AllowedExtensions.trim().split(',');
        if(extensions.length>0)
        {
            if(!extensions.some(x=>name.endsWith(x.toLowerCase().trim()))) {
                ToastService.SendErrorMessage('Invalid file type');
                this.forceUpdate();
                return false;
            }
        }

        return true;
    }


    private OnDragEnter(x: React.DragEvent<HTMLDivElement>,file:FBFileItem) {
        if(!this.IsDragAndDropSupported())
            return;
        x.preventDefault();
        file.Highlight=true;
        this.forceUpdate();
    }

    private OnDragExit(file:FBFileItem) {
        if(!this.IsDragAndDropSupported())
            return;
        file.Highlight=false;
        this.forceUpdate();
    }

    private FileChanged(row: FBFileItem, files:FileList) {
        row.File=files;
        if(files.length==0&&this.Model.Files.length>1)
            this.RemoveFile(row);
        else {
            let name= row.File[0].name;
            if(!this.ExtensionIsValid(name))
            {
                row.File=[];
                row.Ref.value='';
                return;
            }
            row.Name = row.File[0].name;
        }

        if(this.Model.Options.AllowMultipleFiles&&this.Model.Files.filter(x=>x.File.length==0).length==0)
        {
            this.Model.Files.push(this.Model.CreateRow());
        }

        this.forceUpdate(()=>this.Model.FireValueChanged());

    }

    private RemoveFile(row: FBFileItem) {
        if(this.Model.Files.indexOf(row)>=0)
        {
            this.Model.Files.splice(this.Model.Files.indexOf(row),1);

            if(this.Model.Files.length==0)
                this.Model.Files.push(this.Model.CreateRow());
        }

        this.forceUpdate(()=>this.Model.FireValueChanged());
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface FBFileProps extends FBFieldWithPriceBaseProps{
    Model:FBFileModel;

}

interface FBFileState extends FBFieldWithPriceBaseState{

}



EventManager.Subscribe(GetModel,(event)=>{
    if(event.Options.Type==FieldTypeEnum.FileUpload)
        return new FBFileModel(event.Options as FBFileOptions,event.parent).Initialize(event.PreviousData);
});



EventManager.Subscribe(GetFieldOptions,(event)=>{
    if(event.Type==FieldTypeEnum.FileUpload)
        return new FBFileOptions();
});

EventManager.Subscribe(GetField,(event)=>{
    if(event.Model.Options.Type==FieldTypeEnum.FileUpload)
        return <FBFile  Model={event.Model as FBFileModel}/>
});
//#endregion "My Region"