import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {FBFieldWithPriceBaseModel} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Model";

import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";
import {FBFileOptions} from "./FBFile.Options";


export class FBFileModel extends FBFieldWithPriceBaseModel<FBFileOptions,FBColumnModel>{

    Files:FBFileItem[];
    constructor(Options: FBFileOptions, Parent: FBColumnModel) {
        super(Options, Parent);
    }

    public GetValue() {
        return this.Files.filter(x=>x.File.length>0).map(x=>{return {Id:x.Id,Name:x.Name}});
    }

    public GetStoresInformation(): boolean {
        return true;
    }

    public CreateRow(name:string='') {
        return new FBFileItem(name);

    }

    protected InternalSerialize(obj: any) {
        super.InternalSerialize(obj);
        obj.Value=this.GetValue();
    }

    GetIsUsed(): boolean {
        return this.GetValue().length>0;
    }

    ListValueChanged(row:FBFileItem,columnIndex:number,value:string)
    {
        this.Refresh();
    }


    private CreateRowAt(row: FBFileItem) {
        let clonedRow=this.Files.slice(0);
        clonedRow.splice(clonedRow.indexOf(row)+1,0,this.CreateRow());
        this.Files=clonedRow;
        this.FireValueChanged();
    }

    GetDynamicFieldNames(): string[] {
        return ['FBFile'];
    }

    InitializeStartingValues() {

        let previousValues:string[][]=this.GetPreviousDataProperty('Value',[]);
        let valueToUse:FBFileItem[]=[];
        if(previousValues.length==0)
            valueToUse=[this.CreateRow()];

        this.Quantity=this.GetPreviousDataProperty( 'Quantity',this.ParseNumber(this.Options.QuantityDefaultValue));
        this.Files=[this.CreateRow()];
    }

}





export class FBFileItem {
    public Id:number;
    private static _lastId:number=0;
    public Ref:HTMLInputElement;
    public Highlight:boolean;
    public File:any;
    public constructor(public Name: string)
    {
        this.File=[];
        this.Highlight=false;
        this.Id=++FBFileItem._lastId;
        this.FileRefUpdated=this.FileRefUpdated.bind(this);
    }

    public FileRefUpdated(item:HTMLInputElement)
    {
        this.Ref=item as any;
    }

}